* {
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  line-height: 60px;
  display: flex;
}

.header__logo {
  display: block;
}

.nav {
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
}

.nav__ul {
  gap: 20px;
  display: flex;
}

.nav__item {
  list-style: none;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  font-size: 12px;
  text-decoration: none;
  display: block;
}

.nav__link:hover {
  color: #00acdc;
}

.nav .is-active {
  color: #00acdc;
  position: relative;
}

.nav .is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

[data-qa="nav-hover"]:hover {
  color: #00acdc;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  font-family: Roboto, sans-serif;
  transition: transform .3s;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__description {
  flex-direction: column;
  gap: 4px;
  display: flex;
}

.card__description .product-name {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__description .product-code {
  color: #616070;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 16px;
  display: flex;
}

.card__review .review-count {
  height: 14px;
  font-size: 10px;
  font-weight: 400;
  line-height: 16px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 24px;
  display: flex;
}

.card__price .price-text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price .price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy {
  width: 100%;
  height: 40px;
  margin-top: 16px;
}

.card__buy .link-buy {
  color: #fff;
  background: #00acdc;
  border: 0;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.card__buy .link-buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .product-name {
  color: #34568b;
}

.stars {
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--active--1 .stars__star:nth-child(-n+1), .stars--active--2 .stars__star:nth-child(-n+2), .stars--active--3 .stars__star:nth-child(-n+3), .stars--active--4 .stars__star:nth-child(-n+4), .stars--active--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.main {
  --count-col: 1;
  grid-template-columns: repeat(var(--count-col), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    --count-col: 2;
  }
}

@media (width >= 768px) {
  .main {
    --count-col: 3;
  }
}

@media (width >= 1024px) {
  .main {
    --count-col: 4;
  }
}
